import i18n from 'i18next';
import {initReactI18next} from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';

i18n
    .use(LanguageDetector)
    .use(initReactI18next)
    .init({
        debug: true,
        fallbackLng: 'en',
        interpolation: {
            escapeValue: false,
        },
        resources: {
            en: {
                translation: {
                    weather: {
                        cloud: 'cloudly'
                    },
                    btnAdd: 'Add',
                    citySearch: 'Search for a city...',
                    citySearchError: 'Connection error...',
                    Thunderstorm: 'Thunderstorm',
                    Drizzle: 'Drizzle',
                    Rain: 'Rain',
                    Snow: 'Snow',
                    Clear: 'Clear',
                    Clouds: 'Clouds',
                    Mist: 'Mist',
                    Smoke: 'Smoke',
                    Haze: 'Haze',
                    Dust: 'Dust',
                    Fog: 'Fog',
                    Sand: 'Sand',
                    Ash: 'Ash',
                    Squall: 'Squall',
                    Tornado: 'Tornado',
                    wind: 'Wind',
                    humidity: 'Humidity',
                    pressure: 'Pressure',
                    shortWind:'m/s',
                    shortPressure:'Pa',
                    feelsLike: 'Feels like'
                }
            },
            ru: {
                translation: {
                    weather: {
                        cloud: ''
                    },
                    btnAdd: '',
                    citySearch: ' ...',
                    citySearchError: ' ...',
                    Thunderstorm: '',
                    Drizzle: '',
                    Rain: '',
                    Snow: '',
                    Clear: '',
                    Clouds: '',
                    Mist: '',
                    Smoke: '',
                    Haze: '',
                    Dust: '',
                    Fog: '',
                    Sand: '',
                    Ash: '',
                    Squall: '',
                    Tornado: '',
                    wind: '',
                    humidity: '',
                    pressure: '',
                    shortWind:'/c',
                    shortPressure:'',
                    feelsLike: ''
                }
            },
            uk: {
                translation: {
                    weather: {
                        cloud: ''
                    },
                    btnAdd: '',
                    citySearch: ' ...',
                    citySearchError: ' `...',
                    Thunderstorm: '',
                    Drizzle: '',
                    Rain: '',
                    Snow: '',
                    Clear: '',
                    Clouds: '',
                    Mist: '',
                    Smoke: '',
                    Haze: '',
                    Dust: '',
                    Fog: '',
                    Sand: '',
                    Ash: '',
                    Squall: '',
                    Tornado: '',
                    wind: '',
                    humidity: '',
                    pressure: '',
                    shortWind:'/c',
                    shortPressure:'',
                    feelsLike: ''
                }
            }
        }
    });

export default i18n;